<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import './assets/iconfont/iconfont.css'

export default {
  name: 'App'
}
</script>

<style>

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body,ol,ul,h1,h2,h3,h4,h5,h6,p,th,td,dl,dd,form,fieldset,legend,input,textarea,select,td,figure{margin:0;padding:0;}
  body{font-family:"微软雅黑"; word-wrap:break-word;letter-spacing:0.004933rem}
  a,u,s,del{color:#333;text-decoration:none} 
  fieldset,a img,.bor0,input{border:0;}
  i,em,b{font-style:normal;font-weight:100;}
  li{list-style:none}
  img{vertical-align:middle}
  table{border-collapse:collapse;}
  input,textarea{outline:none;}
  textarea{resize:none;}
  mark{background: none;}
  h1,h2,h3,h4,h5,h6{font-weight: normal;}
  *{
    box-sizing:border-box;
  }
  .clear{zoom:1;}
  .clear:after{content:"";display:block;clear:both;}
  .vcenter{vertical-align:middle;display:inline-block;width:0;height:100%;}
  .over{overflow:hidden;text-overflow:ellipsis;white-space:nowrap;}
  .fl{float:left;}
  .fr{float:right;}
  .cp{cursor:pointer;}
  
  .bdr-b:after { height: 1px; content: ''; width: 100%; border-bottom: 1px solid #f0f0f0; position: absolute; bottom: 0; right: 0; transform: scaleY(0.5); -webkit-transform: scaleY(0.5); z-index: 10; }
   .bdr-t:after { height: 1px; content: ''; width: 100%; border-top: 1px solid #f0f0f0; position: absolute; top: 0; right: 0; transform: scaleY(0.5); -webkit-transform: scaleY(0.5); z-index: 10; }
  .hidden{
    text-overflow : ellipsis; 
    white-space : nowrap; 
    overflow : hidden; 
  }
  div,a,input{
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    outline:none;
    background: none;
    text-decoration: none;
}
input{
}
::-webkit-input-placeholder { /* WebKit browsers */   //设置input placeholder字体颜色
  color:#d8d8d8;
} 
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
  color:#d8d8d8;
} 
::-moz-placeholder { /* Mozilla Firefox 19+ */ 
  color:#d8d8d8;
} 
:-ms-input-placeholder { /* Internet Explorer 10+ */ 
  color:#d8d8d8;
}
/*@import '~@/assets/icon/iconfont.css'*/

</style>



