<template>
	<div>
		<cm-header :title="title"></cm-header>
		<br>
		<br>
		<p @click="getimg">点击完成</p>
		<cm-footer :active="active"></cm-footer>
	</div>
</template>
<script>
	import head from '../components/head.vue'
	import foot from '../components/foot.vue'
	import { Toast } from 'mint-ui';
	export default {
		data: function(){
			return {
				title:'店铺',
				active : [false,false,true,false]
			}
		},
		methods : {
			handleClose : function(){
				Toast({
				  message: '操作成功',
				  iconClass: 'mint-toast-icon mintui mintui-success'
				});
			},
			getimg:function(){
				this.$ajax({
			      method: 'post',
			      url: 'http://bxu2442210797.my3w.com/mysql/aboutme.php',
			      dataType:'json'
			    }).then(function(res){
			   	  console.log(res.data)
			    }).catch(function(err){
				  console.log(err)
			    })
			}

		},
		components: {
	     'cm-footer': foot,
	     'cm-header': head
	    }
	}
</script>
<style>
	
</style>


