import Vue from 'vue'
import Router from 'vue-router'
import Index from '../pages/index'
import Cart from '../pages/cart'
import Shop from '../pages/shop'
import Home from '../pages/home'
import Mine from '../pages/mine'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'home',
      component: Home,
      children : [
        {
          path: '/',
          name: 'index',
          component: Index 
        },
        {
          path: '/cart',
          name: 'cart',
          component: Cart
        },
        {
          path: '/shop',
          name: 'shop',
          component: Shop 
        },
        {
          path: '/mine',
          name: 'mine',
          component: Mine 
        }

      ]
    }
    
  ]
})



