<template>
	<div id="foot">
		<div class="foot bdr-t" >
			<router-link :to="item.path" v-for="(item,index) in items" :key="index" >
				<i :style="[active[index] ? activestyle : '']" :class="[iconfont,active[index] ? item.iconActive : item.icon]"></i>
				<p :style="[active[index] ? activestyle : '']">{{item.text}}</p>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data:function(){
			return {
				iconfont : 'iconfont',
				activestyle : {
					color:'red'
				},
				items : [
					{
						text:'首页',
						active : 'active',
						icon :'icon-shouye',
						iconActive : 'icon-shouyefill',
						path : '/'
					},
					{
						text:'购物车',
						active : 'active',
						icon :'icon-gouwuche',
						iconActive : 'icon-gouwuchefill',
						path : '/cart'
					},
					{
						text:'店铺',
						active : 'active',
						icon :'icon-dianpu',
						iconActive : 'icon-dianpufill',
						path : '/shop'
					},
					{
						text:'个人中心',
						active : 'active',
						icon :'icon-wode',
						iconActive : 'icon-wodefill',
						path : '/mine'
					}
				]
			}
		},
		props:{
			active :{
				type: Array,
				default : function(){
					return [true,false,false,false]
				}
			}
		}
	}
</script>
<style>
	#foot{
		position: fixed;
		height: 50px;
		bottom: 0;
		width: 100%;
	}
	.foot{
		height: 50px;
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
	.foot .iconfont{
		font-size: 24px;
	}
	.foot a {
		flex: 1;
		padding-top: 5px;
		font-size: 12px;
	}
</style>


