<template>
	<mt-header :title="title">
      <router-link v-if="headBack" to="/" slot="left">
          <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button v-if="headRight" icon="more" slot="right"></mt-button>
    </mt-header>
</template>
<script>
	export default {
		data : function(){
			return {
				
			}
		},
		props:{
			title :{  // 设置 title 信息
				default : '首页'
			},
			headBack:{ // 设置头部左边的返回按钮 默认不可见
				default:false
			},
			headRight:{ // 设置头部右边的分享按钮 默认不可见
				default:false
			}
		}
	}
</script>
<style>
	
</style>


