<template>
	<div>
		<cm-header :title="title"></cm-header>
		<br>
		<br>
		<p @click="handleClose">点击完成</p>
		<cm-footer :active="active"></cm-footer>
	</div>
</template>
<script>
	import head from '../components/head.vue'
	import foot from '../components/foot.vue'
	import { Toast } from 'mint-ui';
	export default {
		data: function(){
			return {
				title:'个人中心',
				active : [false,false,false,true]
			}
		},
		methods : {
			handleClose : function(){
				Toast({
				  message: '操作成功',
				  iconClass: 'mint-toast-icon mintui mintui-success'
				});
			}
		},
		components: {
	     'cm-footer': foot,
	     'cm-header': head
	    }
	}
</script>
<style>
	
</style>


