		<mt-swipe :auto="0">
		  <mt-swipe-item v-for="(item,index) in food" :key="index" ><a href="" v-for="(itema,indexa) in item" :key="indexa" ><img :src="itema.imgsrc" alt=""></a></mt-swipe-item>
