<style>
	.mint-swipe{
		height: 200px;
		background: #f6f6f6;
	}
	.mint-swipe a{
		display: inline-block;
		float: left;
		width: 25%;
	}
	.mint-swipe a img{
		display: inline-block;
		width: 80%;
	}
	body .mint-swipe:nth-type-of(2){
		height: auto;
	}
</style>


